import * as React from 'react';
import {
  SignalCellular0Bar,
  SignalCellular1Bar,
  SignalCellular2Bar,
  SignalCellular3Bar,
  SignalCellular4Bar,
  SignalCellularNull as UnknownIcon,
} from '@material-ui/icons';

const MIN_SIGNAL_DB = -109;
const MAX_SIGNAL_DB = -53;

const BAR_ICONS = [
  SignalCellular0Bar,
  SignalCellular1Bar,
  SignalCellular2Bar,
  SignalCellular3Bar,
  SignalCellular4Bar,
];

const SINGLE_BAR_DB = (MAX_SIGNAL_DB - MIN_SIGNAL_DB + 1) / BAR_ICONS.length;

// workaround to be used in ToolTip, see https://material-ui.com/guides/composition/#caveat-with-refs
export default React.forwardRef(({ rssiDB, ...props }, ref) => {
  let SignalBarIcon;
  if (typeof rssiDB !== 'number') {
    console.warn('Missing or invalid rssiDB prop');
    SignalBarIcon = UnknownIcon;
  } else {
    const cappedRssiDB = Math.min(
      Math.max(rssiDB, MIN_SIGNAL_DB),
      MAX_SIGNAL_DB
    );
    if (cappedRssiDB < MIN_SIGNAL_DB || cappedRssiDB > MAX_SIGNAL_DB) {
      throw new Error(`Bad bad`);
    }
    SignalBarIcon =
      BAR_ICONS[Math.floor((cappedRssiDB - MIN_SIGNAL_DB) / SINGLE_BAR_DB)];
  }
  return <SignalBarIcon {...props} ref={ref} />;
});
