import React from 'react';
import ReactDOM from 'react-dom';
// import './index.css';
import App from './App';
// import reportWebVitals from './reportWebVitals';
import './register-service-worker';
import 'react-hot-loader';
import { hot } from 'react-hot-loader/root';
// import ReactDOM from '@hot-loader/react-dom';
import { Provider } from 'react-redux';
import { ReactReduxFirebaseProvider } from 'react-redux-firebase';
import { createFirestoreInstance } from 'redux-firestore';
import { firebase } from '@firebase/app';
import { database } from '@firebase/database';

import createStore from './store';
import { IS_PRODUCTION, VERSION } from './config';
import connectFirebase from './firebase';

function getAppContainer() {
  const currentContainer = window.document.getElementById('app');
  if (currentContainer !== null) {
    return currentContainer;
  }
  const newContainer = document.createElement('div');
  newContainer.setAttribute('id', 'app');
  document.body.appendChild(newContainer);
  return newContainer;
}


const globalStyle = `
html, body, #app{
  height: 100%;
  width: 100%;
  margin: 0;
  display: flex;
}
#app > * {
  min-height: 100%;
  width: 100%;
}
`;

connectFirebase();

firebase.database().ref('/connect').set({connect:true})


const store = createStore();

const Main = hot(() => (
  <Provider store={store}>
    <ReactReduxFirebaseProvider
      firebase={firebase}
      config={{
        enableLogging: !IS_PRODUCTION,
      }}
      createFirestoreInstance={createFirestoreInstance}
      dispatch={store.dispatch}
    >
      <>
        <App />
        <style dangerouslySetInnerHTML={{ __html: globalStyle }} />
      </>
    </ReactReduxFirebaseProvider>
  </Provider>
));

ReactDOM.render(<Main />, getAppContainer());

window.version = VERSION;


