const Login = (Props) => {

  
  const GoogeLogin = () => {
    // Props.Complete({Name:"Humza" , Title : "Wow Page"  })
    var provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth()
      .signInWithPopup(provider)
      .then((result) => {
        var user = result.user
        Props.Complete(user)
      }).catch((error) => {
        alert(error.message);
      });
  }



  let userId
  let Logout = false

  try { userId = firebase.auth().currentUser.uid } catch (ex) { userId = undefined }
  console.log(`User Id :: ${userId}`)

  // if (userId && !Logout) {
  //   //firebase logout
  //     alert(userId)
  //     firebase.auth().signOut().then(()=>{
  //       alert(userId)
  //     })
  //   // Logout = true
  // }


  if (!userId) {
    return (
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <GoogleButton
          label="Log in cu Google"
          onClick={() => {
            // firebase.login({ provider: 'google', type: 'popup' });
            GoogeLogin()
          }}
        />
      </div>
    );
  }

  return <div></div>

}

export default compose(
  withFirebase,
  connect(({ firebase: { auth } }) => ({ auth }))
)(Login);
