import { Workbox } from 'workbox-window';
import { IS_PRODUCTION, VAPID_PUBLIC_KEY } from './config';

const promptReload = () => {
  if (!IS_PRODUCTION) {
    return;
  }
  if (
    window.confirm(
      `Se ruleaza o versiune veche a aplicatiei. Apasa OK pentru update.`
    )
  ) {
    window.location.reload();
  }
};
export default (async () => {
  if (IS_PRODUCTION && 'serviceWorker' in navigator) {
    const workbox = new Workbox('/service-worker.js');
    workbox.addEventListener('installed', ({ isUpdate }) => {
      if (isUpdate) {
        promptReload();
      }
    });
    workbox.addEventListener('externalactivated', () => {
      promptReload();
    });
    return await workbox.register();
  }
  return null;
})();
