import * as React from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import {
  Toolbar,
  Typography,
  Tooltip,
  IconButton,
  makeStyles,
} from '@material-ui/core';
import { lighten } from '@material-ui/core/styles';
import { EventBusy as EventBusyIcon } from '@material-ui/icons';
import { useLeavePrompt, useLoadUserDevices } from './hooks';
import Devices from './devices';
import { SELECTED_DEVICES } from './translation';

const useStyles = makeStyles(theme => ({
  highlight: {
    color: theme.palette.secondary.main,
    backgroundColor: lighten(theme.palette.secondary.light, 0.85),
  },
}));

export default compose(
  withRouter,

  // connect(state => ({
  //   devices: state.firestore.ordered.devices || null,
  //   authEmail: state.firebase.auth.email || null,
  // })
  
  // )


)(({ history, authEmail }) => {

  const [selected, setSelected] = React.useState(new Set());
  const hasSelected = selected.size > 0;
  const classes = useStyles();
  // const devices = useLoadUserDevices(authEmail);

  useLeavePrompt(hasSelected);
  const onDeviceClicked = id => history.push(`/device/${id}`);

  return (
    <div style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>

      <Toolbar className={hasSelected ? classes.highlight : undefined}>
        <Typography variant="h6" id="tableTitle">
          {hasSelected ? SELECTED_DEVICES(selected.size) : 'Dispozitive'}
        </Typography>
        {hasSelected ? (
          <>
            <Tooltip title="Sterge evenimente">
              <IconButton>
                <EventBusyIcon />
              </IconButton>
            </Tooltip>
          </>
        ) : null}
      </Toolbar>

      <Devices
        onDeviceClicked={onDeviceClicked}
        // devices={devices}
        selected={[...selected]}
        setSelected={(id, state) => {
          setSelected(old => {
            const set = new Set(old);
            if (state) {
              set.add(id);
            } else {
              set.delete(id);
            }
            return set;
          });
        }}
      />
      
    </div>
  );
});
