const BusyTrueIcon = props => <BusyFalseIcon {...props} color="secondary" />;

const isDefaultAscending = key => {
  switch (key) {
    case 'name':
    case 'isBusy':
      return false;
    default:
      return true;
  }
};

const sortFunction = (sortBy, selected) => {
  switch (sortBy) {
    case 'isSelected': {
      const isSelected = selected.reduce((acc, value) => {
        return { ...acc, [value]: true };
      }, {});
      return (a, b) =>
        (isSelected[a.id] === true ? 1 : 0) -
        (isSelected[b.id] === true ? 1 : 0);
    }
    case 'name':
      return (a, b) => a.name.localeCompare(b.name);
    case 'isBusy':
      return (a, b) => {
        return a.isBusy !== b.isBusy ? 1 : -1;
      };
    case 'lastEvent':
      return (a, b) =>
        a.lastEvent.timeSync.toMillis() - b.lastEvent.timeSync.toMillis();
    default:
      throw new Error(`Unknown sort ${sortBy}`);
  }
};

