import * as React from 'react';
import { Typography, IconButton, Popover } from '@material-ui/core';
import {
  Error as BusyFalseIcon,
  FiberNew as NewIcon,
  AttachMoney as AddCashIcon,
  PowerSettingsNew as PowerIcon,
  Wifi as OnlineIcon,
  WifiOff as OfflineIcon,
  RotateLeft as ResetIcon,
  Help as UnknownIcon,
  AddCircle as AddCreditAdminIcon,
  LockOpenOutlined,
  LockOutlined,
  CreditCard,
  InfoOutlined,
} from '@material-ui/icons';
import SignalBarIcon from './signal-bar-icon';

// eslint-disable-next-line react/jsx-props-no-spreading
const BusyTrueIcon = props => <BusyFalseIcon {...props} color="secondary" />;

const isEventWarning = (type, data) => {
  switch (type) {
    case 'case-open':
      return data.isOpen;
    default:
      return false;
  }
};
const getEventIcon = (type, data) => {
  switch (type) {
    case 'device-created':
      return NewIcon;
    case 'add-cash':
      return AddCashIcon;
    case 'add-card':
      return CreditCard;
    case 'is-busy':
      return data.isBusy ? BusyTrueIcon : BusyFalseIcon;
    case 'hopper-empty':
      return data.isEmpty ? BusyTrueIcon : BusyFalseIcon;
    case 'connectivity':
      return data.isOnline ? OnlineIcon : OfflineIcon;
    case 'startup':
      return PowerIcon;
    case 'reset-credit-total':
      return ResetIcon;
    case 'add-credit-admin':
      if (data.amount !== 0) {
        return AddCreditAdminIcon;
      }
      return ResetIcon;
    case 'case-open':
      return data.isOpen ? LockOpenOutlined : LockOutlined;
    case 'signal':
      return () => <SignalBarIcon rssiDB={data.rssiDB} />;
    default:
      console.error(`Missing icon ${type} for id ${data.id}`);
      return UnknownIcon;
  }
};
function formatEvent(type, data) {
  switch (type) {
    case 'device-created':
      return 'Dispozitiv nou';
    case 'add-cash':
      return `${data.amount.toString().padStart(3, '\xa0')} RON (cash)`;
    case 'add-card':
      return `${data.amount.toString().padStart(3, '\xa0')} RON (card)`;
    case 'add-credit-admin':
      const message =
        data.amount !== 0
          ? `${data.amount} RON (add credit)`
          : 'Resetare acceptor';
      return data.isAcknowledged ? `${message} âœ…` : message;
    case 'is-busy':
      return data.isBusy ? 'Eroare acceptor' : 'Acceptor in functiune';
    case 'hopper-empty':
      return data.isEmpty ? 'Lipsa jetoane' : 'Jetoane prezente';
    case 'connectivity':
      return `${data.isOnline ? 'Online' : 'Offline'}`;
    case 'startup':
      if (data.version !== undefined) {
        return `Start (v${data.version})`;
      }
      return `Start`;
    case 'reset-credit-total':
      return 'Reset total credit';
    case 'case-open':
      return data.isOpen ? 'Carcasa deschisa' : 'Carcasa inchisa';
    case 'signal':
      return `Semnal GSM ${data.rssiDB} DB`;
    default:
      console.log(`Unknown type ${type} for id ${data.id}`);
      return 'Eveniment necunoscut';
  }
}
const BanknoteCount = ({ value, amount, style }) => (
  <div
    style={{
      ...style,
      display: 'flex',
      flexDirection: 'column',
      marginRight: 4,
      marginLeft: 4,
    }}
  >
    <Typography>{`${value}RON`}</Typography>
    <Typography>{`${amount}x`}</Typography>
  </div>
);

const Banknotes = ({ banknotes }) => {
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        marginLeft: 4,
        marginRight: 4,
        marginTop: 4,
        marginBottom: 4,
      }}
    >
      <BanknoteCount value="1" amount={banknotes['1']} />
      <BanknoteCount value="5" amount={banknotes['5']} />
      <BanknoteCount value="10" amount={banknotes['10']} />
      <BanknoteCount value="50" amount={banknotes['50']} />
    </div>
  );
};

export default ({ event, style }) => {
  const { type, ...data } = event;
  const Icon = getEventIcon(type, data);
  const isWarning = isEventWarning(type, data);
  const [popoverAnchor, setPopoverAnchor] = React.useState(null);
  const handleInfoClick = event => {
    setPopoverAnchor(event.currentTarget);
  };
  const handleInfoClose = () => {
    setPopoverAnchor(null);
  };
  return (
    <div
      style={{
        ...style,
        display: 'flex',
        alignItems: 'center',
        color: isWarning ? '#c60000' : undefined,
      }}
    >
      <Icon />
      <Typography variant="body2" noWrap>
        {formatEvent(type, data)}
      </Typography>
      {event.type === 'add-cash' ||
      (event.type === 'add-card' && event.banknotes !== null) ? (
        <>
          <IconButton onClick={handleInfoClick}>
            <InfoOutlined />
          </IconButton>
          <Popover
            transitionDuration={0}
            open={popoverAnchor !== null}
            anchorEl={popoverAnchor}
            onClose={handleInfoClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'center',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'center',
            }}
          >
            <Banknotes banknotes={event.banknotes} />
          </Popover>
        </>
      ) : null}
    </div>
  );
};
