import { share, map } from 'rxjs/operators';
import { interval, Subject } from 'rxjs';
import { useSelector } from 'react-redux';
import { useFirestoreConnect } from 'react-redux-firebase';
import { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import { API_URL } from './config';

export const refreshAddCreditSubject = new Subject();

export const refreshAddCreditEvents = () => {
  refreshAddCreditSubject.next(null);
};

const source = interval(1000).pipe(
  map(() => new Date().getTime()),
  share()
);

export const useTime = () => {
  const [time, setTime] = useState(new Date().getTime());
  useEffect(() => {
    const sub = source.subscribe(newTime => {
      setTime(newTime);
    });
    return () => sub.unsubscribe();
  }, []);
  return time;
};

export const useLeavePrompt = isActive => {
  useEffect(() => {
    if (isActive) {
      const cb = e => {
        e.preventDefault();
        // browsers do not support custom text
        // https://stackoverflow.com/questions/38879742/is-it-possible-to-display-a-custom-message-in-the-beforeunload-popup
        e.returnValue = '';
      };
      window.addEventListener('beforeunload', cb);

      return () => {
        window.removeEventListener('beforeunload', cb);
      };
    }

    return undefined;
  }, [isActive]);
};

export const useResetReactWindowCache = data => {
  // https://github.com/bvaughn/react-window/issues/147#issue-414996310
  const ref = useRef();
  useEffect(() => {
    // AutoSizer causes ref not being present on the component mount
    if (ref.current) {
      ref.current.resetAfterIndex(0);
    }
  }, [data]);
  return ref;
};

export const useUserData = email => {
  const userKey = `email:${email}`;
  const storeKey = `userData-${userKey}`;
  useFirestoreConnect(() => {
    return email !== null
      ? [
          {
            collection: 'emails',
            doc: email,
            storeAs: storeKey,
          },
        ]
      : [];
  });
  return useSelector(state => {
    if (email === null) {
      return null;
    }
    return state.firestore.data[storeKey] || null;
  });
};

export const useSignalData = deviceID => {
  const storeKey = `deviceSignal-${deviceID}`;
  useFirestoreConnect(() => [
    {
      collection: 'devices',
      doc: deviceID,
      orderBy: [['timeSync', 'desc']],
      subcollections: [{ collection: 'state' }],
      storeAs: storeKey,
    },
  ]);
  return useSelector(state => {
    return state.firestore.ordered[storeKey] || [];
  });
};

const notificationPermissionSubject = new Subject();
const hasWebPushSupport =
  'Notification' in window && 'serviceWorker' in window.navigator;

export const useNotificationPermission = () => {
  const [notificationPermission, setNotificationPermission] = useState(
    hasWebPushSupport ? Notification.permission : 'unsupported'
  );
  useEffect(() => {
    const sub = notificationPermissionSubject.subscribe(
      setNotificationPermission
    );
    return () => {
      sub.unsubscribe();
    };
  }, []);
  return {
    notificationPermission,
    triggerPrompt: async () => {
      if (hasWebPushSupport) {
        const newPermission = await Notification.requestPermission();
        notificationPermissionSubject.next(newPermission);
      }
    },
  };
};
export const useIsUserAdmin = email => {
  const userData = useUserData(email);
  return userData?.isAdmin || false;
};

export const useAuthEmail = () => {
  return useSelector(state => state.firebase?.auth.email || null);
};

export const useLoadUserDevices = email => {
  const userKey = `email:${email}`;
  const storeKey = `devices-${userKey}`;
  useFirestoreConnect(() => [
    {
      collection: 'devices',
      where: [['admins', 'array-contains', userKey]],
      storeAs: storeKey,
    },
  ]);
  return useSelector(state => state.firestore.ordered[storeKey] || null);
};

export const useLoadDeviceAddCreditEvents = deviceID => {
  const [events, setEvents] = useState([]);
  const [retryIdx, setRetryIdx] = useState(0);
  useEffect(() => {
    const int = setInterval(() => {
      setRetryIdx(Math.random());
    }, 5000);
    const sub = refreshAddCreditSubject.subscribe(() => {
      setRetryIdx(Math.random());
    });
    return () => {
      clearInterval(int);
      sub.unsubscribe();
    };
  }, []);
  useEffect(() => {
    const { CancelToken } = axios;
    const cancelTokenSource = CancelToken.source();
    // axios.get(`${API_URL}/add-credit/${deviceID}`, {
    //     cancelToken: cancelTokenSource.token,
    //   }).then(it => {
    //     setEvents(it.data);
    //   }).catch(e => {});

    return () => {
      cancelTokenSource.cancel();
    };
  }, [deviceID, retryIdx]);
  return events;
};

export const useLoadAllDevices = () => {
  useFirestoreConnect(() => [
    {
      collection: 'devices',
      storeAs: 'allDevices',
    },
  ]);
  return useSelector(state => state.firestore.ordered.allDevices || null);
};
