import * as React from 'react';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { isEmail } from 'validator';
import {
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Typography,
  Toolbar,
  Tooltip,
  IconButton,
  Fab,
  Dialog,
  DialogTitle,
  DialogContentText,
  TextField,
  DialogContent,
  DialogActions,
  Button,
  Checkbox,
  makeStyles,
} from '@material-ui/core';
import { EventBusy as EventBusyIcon, Add } from '@material-ui/icons';
import { lighten } from '@material-ui/core/styles';

import { withRouter } from 'react-router-dom';

import TableSortLabel from '@material-ui/core/TableSortLabel';
import stable from 'stable';
import { EMAIL, SELECTED_USERS } from '../translation';
import Loading from '../loading';

const isDefaultAscending = (key) => {
  switch (key) {
    case 'email':
    case 'isAdmin':
      return false;
    default:
      return true;
  }
};

const sortFunction = sortBy => {
  switch (sortBy) {
    case 'email':
      return (a, b) => a.email.localeCompare(b.name);
    case 'isAdmin':
      return (a, b) => {
        return a.isAdmin !== b.isAdmin ? 1 : -1;
      };
    default:
      throw new Error(`Unknown sort ${sortBy}`);
  }
};


const sort = (data, comparator, isAscending) => {
  const descSortedData = stable(data, comparator);
  return isAscending ? descSortedData.reverse() : descSortedData;
};
const columns = [
  { key: 'email', label: EMAIL },
  { key: 'isAdmin', label: 'Administrator' },
];

const useStyles = makeStyles(theme => ({
  highlight: {
    color: theme.palette.secondary.main,
    backgroundColor: lighten(theme.palette.secondary.light, 0.85),
  },
}));
const Users = ({ emails, history, firestore }) => {
  const addUser = newEmail => {
    firestore
      .collection('emails')
      .doc(newEmail)
      .set({ isAdmin: false });
  };
  const deleteUsers = deleteEmails => {
    const batch = firestore.batch();
    deleteEmails.forEach(email => {
      batch.delete(firestore.collection('emails').doc(email));
    });
    batch.commit();
  };
  const [showAddUser, setShowAddUser] = React.useState(false);
  const [isInvalidEmail, setIsInvalidEmail] = React.useState(false);
  const [newEmail, setNewEmail] = React.useState('');

  const closeModal = () => {
    setShowAddUser(false);
    setIsInvalidEmail(false);
  };

  const classes = useStyles();
  const [selected, setSelected] = React.useState(new Set());
  const hasSelected = selected.size > 0;
  const [orderBy, setOrderBy] = React.useState('email');
  const [orderAscending, setOrderAscending] = React.useState(
    isDefaultAscending(orderBy)
  );

  if (emails === null) {
    return <Loading label="Se incarca lista de dispozitive" />;
  }

  const toggleOrderBy = key => {
    if (orderBy === key) {
      setOrderAscending(old => !old);
    } else {
      setOrderBy(key);
      setOrderAscending(isDefaultAscending(key));
    }
  };

  const toggleSelection = id => {
    setSelected(old => {
      const set = new Set(old);
      if (set.has(id)) {
        set.delete(id);
      } else {
        set.add(id);
      }
      return set;
    });
  };

  const createViewUserNav = email => () => history.push(`/users/${email}`);

  const sortedEmails = sort(emails, sortFunction(orderBy), orderAscending);
  const trySubmitEmail = () => {
    if (isEmail(newEmail)) {
      addUser(newEmail);
      setShowAddUser(false);
    } else {
      setIsInvalidEmail(true);
    }
  };
  return (
    <div>
      <Toolbar className={hasSelected ? classes.highlight : undefined}>
        <Typography variant="h6" id="tableTitle">
          {hasSelected ? SELECTED_USERS(selected.size) : 'Utilizatori'}
        </Typography>
        {hasSelected ? (
          <>
            <Tooltip title="Sterge utilizatori">
              <IconButton
                onClick={() => {
                  deleteUsers([...selected]);
                  setSelected(old => {
                    const set = new Set(old);
                    selected.forEach(email => {
                      set.delete(email);
                    });
                    return set;
                  });
                }}
              >
                <EventBusyIcon />
              </IconButton>
            </Tooltip>
          </>
        ) : null}
      </Toolbar>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell />
            {columns.map(({ key, label, span = 1 }, idx) => (
              <TableCell
                key={key}
                colSpan={span}
                align={idx === columns.length - 1 ? 'center' : 'inherit'}
              >
                <TableSortLabel
                  active={orderBy === key}
                  direction={
                    (orderBy === key
                      ? orderAscending
                      : isDefaultAscending(key))
                      ? 'asc'
                      : 'desc'
                  }
                  onClick={() => toggleOrderBy(key)}
                >
                  {label}
                </TableSortLabel>
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {sortedEmails.map(({ email, isAdmin }) => {
            const isSelected = selected.has(email);
            return (
              <TableRow
                key={email}
                hover
                selected={isSelected}
                to={`/device/${email}`}
              >
                <TableCell
                  padding="checkbox"
                  onClick={() => toggleSelection(email)}
                >
                  <Checkbox checked={isSelected} />
                </TableCell>
                <TableCell onClick={createViewUserNav(email)}>
                  <Typography noWrap>{email}</Typography>
                </TableCell>
                <TableCell onClick={createViewUserNav(email)}>
                  <Typography noWrap>{isAdmin ? 'is admin' : ''}</Typography>
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
      <Fab
        color="primary"
        style={{ position: 'absolute', right: 16, bottom: 16 }}
        onClick={() => {
          setShowAddUser(true);
        }}
      >
        <Add />
      </Fab>

      <Dialog open={showAddUser} onClose={closeModal}>
        <DialogTitle>Adaugare user</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Va rog sa adaugati o adresa de email asociata cu un cont Google
          </DialogContentText>
          <TextField
            error={isInvalidEmail}
            helperText={isInvalidEmail ? 'email invalid' : undefined}
            onChange={({ target }) => {
              setNewEmail(target.value);
              setIsInvalidEmail(false);
            }}
            onKeyDown={event => {
              if (event.keyCode === 13) {
                trySubmitEmail();
                event.preventDefault();
              }
            }}
            autoFocus
            margin="dense"
            id="name"
            label="Adresa Email"
            type="email"
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={closeModal}>Anulare</Button>
          <Button onClick={trySubmitEmail} color="primary">
            Adaugare
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};
export default compose(
  withRouter,
  connect(state => ({
    emails:
      state.firestore.ordered.emails?.map(({ id, isAdmin }) => ({
        email: id,
        isAdmin,
      })) || null,
  })),
  firestoreConnect([
    {
      collection: 'emails',
    },
  ])
)(Users);
