import * as React from 'react';
import { Typography } from '@material-ui/core';
import { useTime } from './hooks';
import { TIME_RELATIVE, TIME_ABSOLUTE } from './translation';

export default ({ timestamp, style }) => {
  const now = useTime();

  return (
    <div
      style={{
        ...style,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
      }}
    >
      <Typography variant="body2" noWrap>
        {TIME_RELATIVE(timestamp, now)}
      </Typography>
      <Typography variant="subtitle2" noWrap>
        {TIME_ABSOLUTE(timestamp)}
      </Typography>
    </div>
  );
};
