const crypto = require('crypto');
const baseX = require('base-x');

const base56 = baseX(
  '23456789ABCDEFGHJKLMNPQRSTUVWXYZabcdefghijkmnpqrstuvwxyz'
);

// this is the same entropy used by Youtube
// https://webapps.stackexchange.com/questions/13854/are-youtube-codes-guaranteed-to-always-be-11-characters
module.exports = (byteCount = 8) =>
  base56.encode(crypto.randomBytes(byteCount));
