import * as React from 'react';
import { useFirestore } from 'react-redux-firebase';
import { FormControlLabel, Checkbox, Typography } from '@material-ui/core';
import { firebase } from '@firebase/app';
import Devices from '../devices';
import Loading from '../loading';
import { useIsUserAdmin, useLoadAllDevices } from '../hooks';

export default ({ email }) => {
  const isAdmin = useIsUserAdmin(email);

  const devices = useLoadAllDevices();
  const firestoreDB = useFirestore();
  if (devices === null) {
    return <Loading label="Se incarca lista de dispozitive" />;
  }
  const isAdminCheckbox = (
    <Checkbox
      checked={isAdmin}
      onChange={({ target }) => {
        const userRef = firestoreDB.collection('emails').doc(email);
        userRef.update({
          isAdmin: target.checked,
        });
      }}
    />
  );

  return (
    <div style={{ display: 'flex', flexDirection: 'column' }}>
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
        }}
      >
        <Typography style={{ margin: '0 16px' }}>
          {`Email: ${email}`}
        </Typography>
        <FormControlLabel control={isAdminCheckbox} label="Administrator" />
      </div>
{/* 
      <Devices
        selectionLabel="Acces permis"
        devices={devices}
        selected={devices
          .filter(({ admins = [] }) => admins.includes(`email:${email}`))
          .map(({ id }) => id)}
        setSelected={(id, state) => {
          const deviceRef = firestoreDB.collection('devices').doc(id);
          deviceRef.update({
            admins: (state
              ? firebase.firestore.FieldValue.arrayUnion
              : firebase.firestore.FieldValue.arrayRemove)(`email:${email}`),
          });
        }}
      /> */}
    </div>
  );
};
