// todo: use Ponyfill when https://github.com/formatjs/formatjs/issues/201 lands
delete Intl.RelativeTimeFormat;
require('@formatjs/intl-relativetimeformat/polyfill');
require('@formatjs/intl-relativetimeformat/locale-data/ro');

delete Intl.PluralRules;
require('@formatjs/intl-pluralrules/polyfill');
require('@formatjs/intl-pluralrules/locale-data/ro');

const cardinalRules = new Intl.PluralRules('ro', { type: 'cardinal' });
const relativeTimeFormat = new Intl.RelativeTimeFormat('ro', {
  numeric: 'auto',
});
const absoluteTimeFormat = new Intl.DateTimeFormat('ro', {
  minute: 'numeric',
  hour: 'numeric',
});

const absoluteMonthCurrentYearFormat = new Intl.DateTimeFormat('ro', {
  month: 'long',
});

const absoluteMonthOtherYearFormat = new Intl.DateTimeFormat('ro', {
  month: 'long',
  year: 'numeric',
});

const absoluteDayCurrentYearFormat = new Intl.DateTimeFormat('ro', {
  day: 'numeric',
  month: 'long',
});

const absoluteDayOtherYearFormat = new Intl.DateTimeFormat('ro', {
  day: 'numeric',
  month: 'long',
  year: 'numeric',
});

const absoluteDayWeekFormat = new Intl.DateTimeFormat('ro', {
  weekday: 'long',
});

const cardinal = count => cardinalRules.select(count);

export const SELECTED_DEVICES = count => {
  switch (cardinal(count)) {
    case 'one':
      return `${count} dispozitiv selectat`;
    case 'few':
      return `${count} dispozitive selectate`;
    case 'other':
      return `${count} de dispozitive selectate`;
    default:
      throw new Error();
  }
};

export const SELECTED_USERS = count => {
  switch (cardinal(count)) {
    case 'one':
      return `${count} utilizator selectat`;
    case 'few':
      return `${count} utilizatori selectati`;
    case 'other':
      return `${count} de utilizatori selectati`;
    default:
      throw new Error();
  }
};

export const TIME_RELATIVE = (time, now = new Date().getTime()) => {
  const deltaMilliseconds = time - now;
  const deltaSeconds = deltaMilliseconds / 1000;
  const deltaMinutes = deltaSeconds / 60;
  if (Math.abs(deltaMinutes) < 1) {
    return relativeTimeFormat.format(Math.trunc(deltaSeconds), 'second');
  }
  const deltaHours = deltaMinutes / 60;
  if (Math.abs(deltaHours) < 1) {
    return relativeTimeFormat.format(Math.trunc(deltaMinutes), 'minute');
  }
  const deltaDays = deltaHours / 24;
  if (Math.abs(deltaDays) < 1) {
    return relativeTimeFormat.format(Math.trunc(deltaHours), 'hour');
  }
  const deltaMonths = deltaDays / (365 / 12);
  if (Math.abs(deltaMonths) < 1) {
    return relativeTimeFormat.format(Math.trunc(deltaDays), 'day');
  }
  const deltaYears = deltaMonths / 12;
  if (Math.abs(deltaYears) < 1) {
    return relativeTimeFormat.format(Math.trunc(deltaMonths), 'month');
  }
  return relativeTimeFormat.format(Math.trunc(deltaYears), 'year');
};

// todo: cap intervals at beginning of month/day for last month, last day, last year
export const DAY_RELATIVE = (time, now = new Date().getTime()) => {
  const midnight = new Date(time);
  midnight.setHours(0, 0, 0, 0);
  const timestamp = midnight.getTime();

  const deltaMilliseconds = timestamp - now;
  const deltaSeconds = deltaMilliseconds / 1000;
  const deltaMinutes = deltaSeconds / 60;
  const deltaHours = deltaMinutes / 60;
  const deltaDays = deltaHours / 24;
  const deltaMonths = deltaDays / (365 / 12);

  if (Math.abs(deltaMonths) < 1) {
    return relativeTimeFormat.format(Math.trunc(deltaDays), 'day');
  }
  const deltaYears = deltaMonths / 12;
  if (Math.abs(deltaYears) < 1) {
    return relativeTimeFormat.format(Math.trunc(deltaMonths), 'month');
  }
  return relativeTimeFormat.format(Math.trunc(deltaYears), 'year');
};

const CURRENT_YEAR = new Date().getFullYear();

export const MONTH_ABSOLUTE = time => {
  const date = new Date(time);
  const absoluteDateFormat =
    date.getFullYear() === CURRENT_YEAR
      ? absoluteMonthCurrentYearFormat
      : absoluteMonthOtherYearFormat;
  return absoluteDateFormat.format(date);
};

export const DAY_WEEK = time => {
  const date = new Date(time);
  return absoluteDayWeekFormat.format(date);
};

export const DAY_ABSOLUTE = time => {
  const date = new Date(time);
  const absoluteDateFormat =
    date.getFullYear() === CURRENT_YEAR
      ? absoluteDayCurrentYearFormat
      : absoluteDayOtherYearFormat;
  return absoluteDateFormat.format(date);
};

export const DAY_TIME_ABSOLUTE = time => {
  const date = new Date(time);
  return absoluteTimeFormat.format(date);
};

export const TIME_ABSOLUTE = time => {
  const date = new Date(time);
  return `${DAY_ABSOLUTE(date)} ${absoluteTimeFormat.format(date)}`;
};

export const NAME = 'Denumire';
export const EMAIL = 'Email';
