import * as React from 'react';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import { connect } from 'react-redux';
import {
  TextField,
  Tab,
  Tabs,
  Button,
  Typography,
  Tooltip,
} from '@material-ui/core';
import { Link, useRouteMatch } from 'react-router-dom';
import Loading from '../loading';
import { NAME } from '../translation';
import DeviceEvents from './events';
import CashMonthly from './cash-monthly';
import CashTransaction from './cash-transaction';
import Notifications from './notifications';
import DayTime from '../day-time';
import SignalBarIcon from '../signal-bar-icon';
import GsmSignal from './gsm-signal';
import { useLoadDeviceAddCreditEvents } from '../hooks';
import { addCredit } from '../user-api';

const generateID = require('../generate-id');

const Device = ({
  firestore,
  device,
  id: deviceID,
  events: firestoreEvents,
}) => {
  //if (device === null || firestoreEvents === null) {
    return <Loading label="Se incarca dispozitivul" />;
 // }

  const [addCreditValue, setAddCreditValue] = React.useState(5);
  const baseURL = useRouteMatch().url;
  const addCreditEvents = useLoadDeviceAddCreditEvents(deviceID).map(
    ({ id, time, amount, isAcknowledged }) => ({
      amount,
      id,
      timeLocal: firestore.Timestamp.fromMillis(time),
      timeSync: firestore.Timestamp.fromMillis(time),
      type: 'add-credit-admin',
      isAcknowledged,
    })
  );
  const events = [...firestoreEvents, ...addCreditEvents].sort(
    (a, b) =>
      (b.timeSync || b.timeLocal).toMillis() -
      (a.timeSync || a.timeLocal).toMillis()
  );

  const screens = [
    {
      component: DeviceEvents,
      title: 'Evenimente',
      path: 'events',
    },
    {
      component: CashMonthly,
      title: 'Raport Bani Lunar',
      path: 'cash-report',
    },
    {
      component: CashTransaction,
      title: 'Raport Bani Tranzactii',
      path: 'cash-transaction',
    },
    {
      component: Notifications,
      title: 'Notificari',
      path: 'notifications',
    },
    {
      component: GsmSignal,
      title: 'Semnal GSM',
      path: 'gsm-signal',
    },
  ].map(({ component, title, path }) => ({
    component,
    title,
    path,
    fullPath: `${baseURL}/${path}`,
  }));

  let startupPath = null;
  for (const { path, fullPath } of screens) {
    const match = useRouteMatch({ path: fullPath, exact: path === '/' });
    if (match !== null) {
      startupPath = path;
    }
  }
  const [activePath, setActivePath] = React.useState(
    startupPath !== null ? startupPath : screens[0].path
  );
  const ActiveTab = screens.find(({ path }) => path === activePath).component;
  const deviceRef = firestore.collection('devices').doc(deviceID);
  const eventsWithResetTotal = [];
  const deviceMoneyResetTimestamp = device.moneyReset?.toMillis() || 0;
  let resetTotalCash = 0;
  let resetTotalCard = 0;
  for (let i = events.length - 1; i >= 0; i -= 1) {
    const event = events[i];
    if (event.type === 'reset-credit-total') {
      resetTotalCash = 0;
      resetTotalCard = 0;
    }
    if (event.type === 'add-cash') {
      resetTotalCash += event.amount;
      eventsWithResetTotal.unshift({
        ...event,
        resetTotalCash,
        resetTotalCard,
      });
    } else if (event.type === 'add-card') {
      resetTotalCard += event.amount;
      eventsWithResetTotal.unshift({
        ...event,
        resetTotalCash,
        resetTotalCard,
      });
    } else {
      eventsWithResetTotal.unshift(event);
    }
  }
  const deviceWithID = { ...device, id: deviceID };
  return (
    <div style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          flexWrap: 'wrap',
        }}
      >
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            margin: '0 16px',
          }}
        >
          <Tooltip
            title={
              typeof device.rssiDB === 'number'
                ? `${device.rssiDB} db`
                : 'Semnal GSM necunoscut'
            }
          >
            <SignalBarIcon
              rssiDB={device.rssiDB}
              style={{ paddingTop: 24, marginRight: 12 }}
            />
          </Tooltip>
          <TextField
            label={NAME}
            value={device.name}
            onChange={({ target: { value } }) => {
              deviceRef.update({ name: value });
            }}
            margin="normal"
          />
          <div style={{ width: 24 }} />
          <TextField
            label="Adaugare credit"
            defaultValue={addCreditValue.toString()}
            type="number"
            onChange={({ target: { value } }) => {
              setAddCreditValue(Number(value));
            }}
            margin="normal"
          />
          <Button
            onClick={() => {
              const confirmed = window.confirm(
                `Sigur doriti sa adaugati ${addCreditValue} credit pentru ${device.name}?`
              );
              if (confirmed) {
                addCredit(deviceID, Number(addCreditValue));
              }
            }}
          >
            Adaugare credit
          </Button>
        </div>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            margin: '0 16px',
          }}
        >
          <Button
            onClick={() => {
              addCredit(deviceID, 0);
            }}
          >
            Resetare acceptor
          </Button>
          <Button
            onClick={() => {
              const confirmed = window.confirm(
                `Sigur doriti sa resetati credit-ul pentru ${device.name}?`
              );
              if (confirmed) {
                const now = firestore.Timestamp.now();
                deviceRef
                  .collection('events')
                  .doc(generateID())
                  .set({
                    type: 'reset-credit-total',
                    timeSync: firestore.FieldValue.serverTimestamp(),
                    timeLocal: now,
                  });
                deviceRef.update({
                  moneyReset: now,
                });
              }
            }}
          >
            Resetare credit
          </Button>
          <div
            style={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
            <Typography align="center" variant="subtitle2">
              Ultimul reset la credit
            </Typography>
            <DayTime
              timestamp={deviceMoneyResetTimestamp}
              style={{ alignItems: 'center' }}
            />
          </div>
        </div>
      </div>
      <Tabs
        variant="scrollable"
        scrollButtons="on"
        value={activePath}
        onChange={(_, newPath) => {
          setActivePath(newPath);
        }}
      >
        {screens.map(({ path, title }) => (
          <Tab
            key={path}
            value={path}
            component={Link}
            label={title}
            to={`${baseURL}/${path}`}
          />
        ))}
      </Tabs>
      <ActiveTab
        events={eventsWithResetTotal.map(
          ({ timeSync, timeLocal, ...event }) => {
            return { ...event, timeLocal, timeSync: timeSync || timeLocal };
          }
        )}
        device={deviceWithID}
      />
    </div>
  );
};

export default compose(
  firestoreConnect(props => [
    {
      collection: 'devices',
      doc: props.id,
      storeAs: 'device',
    },
    {
      collection: 'devices',
      doc: props.id,
      orderBy: [['timeSync', 'desc']],
      subcollections: [{ collection: 'events' }],
      storeAs: 'events',
    },
  ]),
  connect(state => ({
    device: state.firestore.data.device || null,
    events: state.firestore.ordered.events || null,
  }))
)(Device);
