import * as React from 'react';
import { Typography, Button } from '@material-ui/core';
import { withFirebase } from 'react-redux-firebase';

const MissingPermission = ({ firebase }) => (
  <div
    style={{
      display: 'flex',
      flex: 1,
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
    }}
  >
    <Typography style={{ marginBottom: 12 }}>
      Acest cont nu are permisione pentru a vizualiza datele
    </Typography>
    <Button
      variant="contained"
      color="primary"
      onClick={() => {
        firebase.logout();
      }}
    >
      Log out
    </Button>
  </div>
);

export default withFirebase(MissingPermission);
