import { firebase } from '@firebase/app';
import '@firebase/auth';
import '@firebase/firestore';
import { FIREBASE_CONFIG, IS_PRODUCTION, DISABLE_NETWORK } from './config';

let firebaseApp = null;
export default async () => {
  if (firebaseApp !== null) {
    return firebaseApp;
  }
  let firebaseConfig;
  if (IS_PRODUCTION && window.location.protocol === 'https:') {
    // Only works if hosted on Firebase
    // https://stackoverflow.com/questions/40856179/custom-authdomain-in-firebase
    firebaseConfig = {
      ...FIREBASE_CONFIG,
      authDomain: window.location.hostname,
    };
  } else {
    firebaseConfig = FIREBASE_CONFIG;
  }

  firebaseApp = firebase.initializeApp(firebaseConfig);
  const firestoreDB = firebaseApp.firestore();
  await firestoreDB.enablePersistence({ synchronizeTabs: true });
  if (DISABLE_NETWORK) {
    await firestoreDB.disableNetwork();
  }
  if (!IS_PRODUCTION) {
    window.firebase = firebase;
    window.app = firebaseApp;
    window.firestoreDB = firestoreDB;
  }
  return firebase;
};
